import styled from 'styled-components';

export const Container = styled.div`
  padding-top: 30px;
  
  width: 100%;
  min-height: 100vh;
  display: flex;
  justify-content: center;
  align-items: center;
  background: var(--main-color);
  flex-direction: column;

  > img {
    width: 200px;
    height: 150px;
  }
  > div {
    width: 60%;
    margin: 10px auto;
    
    > h1 {
      font-weight: bold;
      color: white;
      font-size: 3em;
      line-height: 3em;
    }
    > p {
      font-weight: bold;
      color: white;
      font-size: 2em;
      line-height: 2em;
    }
  }
  
`;