import React from 'react';

import {
  Container,
} from './MainPageStyles';

import Logo from '../../Assets/Images/logo-light-border.png';
import App from '../../Assets/Images/market-graphic.png';

function MainPage() {
  return (
    <Container>
      <img src={Logo} alt="logo" />
      <div>
        <h1>
        ✅ Sobre o app
        </h1>
        <p>
        ➡ O  app é um marketplace focado no mercado fitness
        </p>
        <p>
        ➡ Tem o compromisso de forcener sempre produtos relevantes para este nicho
        </p>
        <p>
        ➡ Contamos com a disposição de espaços privativos no app para cada empresa parceira
        </p>

        <p>
        ➡ Fornecemos um sistema de avaliação dinâmico que contribui para uma melhor tomada de decisão dos usuários na hora da compra
        </p>

        <p>
        ➡ Oferecemos um sistema de notificação do usuário que permite ás empresas anunciarem seus lançamentos ou fomentarem o público com campanhas de marketing
        </p>

        <h1>
        ✅ História
        </h1>

        <p>
        ➡ O app nasceu da demanda de um espaço de anúncio de produtos do nicho fitness na palma da mão dos usuários
        </p>

        <h1>
        ✅ O app
        </h1>

        <img src={App} alt="screenshot app" />

      </div>
    </Container>
  );
}

export default MainPage;